@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

body {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  background-color: #fcfcfc;
}

.navbar {
  display: flex;
  flex-direction: row;
  background-color: #09357b;
  padding: 2rem 2rem;
}

@media (max-width: 768px) {
  .navbar {
    display: flex;
    flex-direction: row;
    padding: 2rem 2rem;
  }
}

.container {
  margin: 0 15rem;
}

@media (max-width: 768px) {
  .container {
    margin: 0 2rem;
  }
}

.logo {
}

@media (max-width: 768px) {
  .logo {
  }
}

.nav-elements {
  margin-left: auto;
  margin-right: 15rem;
  margin-top: 2rem;
  border-bottom: 3px solid #fff;
  cursor: pointer;
}

@media (max-width: 768px) {
  .nav-elements {
    margin-left: auto;
    margin-right: 0;
    margin-top: 2rem;
    border-bottom: 3px solid #fff;
    cursor: pointer;
  }
}

form {
  position: relative;
  padding: 15px;
}

input[type="search"] {
  background-image: url("../../../public/images/search.svg");
  background-position: 220px 6px;
  background-repeat: no-repeat;
  padding-left: 40px;
  border: none;
  background-color: grey;
  opacity: 0.3;
  margin: 0;
  padding: 7px 8px;
  font-size: 14px;
  margin-top: -13px;
  width: 250px;
}

@media (max-width: 768px) {
  input[type="search"] {
    width: 200px;
    margin-top: 40px;
    margin-left: -200px;
  }
}

input[type="search"]::placeholder {
  color: white;
}

